 * AutomataControls™ Remote Portal
 * Database Component Styles - Light Theme
 */

.database-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #f9fafb;
  color: #111827;
}

.database-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem;
  background: white;
  border-bottom: 1px solid #e5e7eb;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}

.database-header h1 {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1.5rem;
  margin: 0;
  color: #111827;
  font-weight: 600;
}

.database-header h1 i {
  color: #14b8a6;
}

.header-actions {
  display: flex;
  gap: 0.75rem;
}

.btn-mode,
.btn-refresh {
  padding: 0.5rem 1rem;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  color: #374151;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.2s;
  font-size: 0.875rem;
  font-weight: 500;
}

.btn-mode:hover,
.btn-refresh:hover {
  background: #f3f4f6;
  border-color: #14b8a6;
  color: #14b8a6;
  transform: translateY(-1px);
}

.error-message {
  padding: 1rem;
  background: #fef2f2;
  border-left: 4px solid #ef4444;
  color: #dc2626;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin: 1rem;
  border-radius: 0.375rem;
}

.database-layout {
  flex: 1;
  display: flex;
  overflow: hidden;
  padding: 1rem;
  gap: 1rem;
}

/* Database Sidebar */
.database-sidebar {
  width: 240px;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  overflow-y: auto;
  padding: 1rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}

.database-sidebar h3 {
  margin: 0 0 0.75rem 0;
  font-size: 0.875rem;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 600;
}

.db-item {
  margin-bottom: 0.25rem;
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 0.375rem;
  padding: 0.375rem 0.5rem;
  cursor: pointer;
  transition: all 0.2s;
}

.db-item:hover {
  background: #f3f4f6;
  border-color: #14b8a6;
  transform: translateX(2px);
}

.db-item.active {
  background: linear-gradient(135deg, #14b8a6 0%, #0d9488 100%);
  color: white;
  border-color: transparent;
  box-shadow: 0 2px 4px rgba(20, 184, 166, 0.2);
}

.db-item-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.db-name {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  font-size: 0.8125rem;
  font-weight: 500;
}

.db-name i {
  font-size: 0.6875rem;
  opacity: 0.8;
}

.db-info-right {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.db-size {
  font-size: 0.6875rem;
  color: #9ca3af;
  min-width: 50px;
  text-align: right;
}

.db-item.active .db-size {
  color: rgba(255, 255, 255, 0.85);
}

.btn-cleanup {
  padding: 0.125rem 0.375rem;
  background: transparent;
  border: 1px solid #d1d5db;
  border-radius: 0.25rem;
  color: #6b7280;
  cursor: pointer;
  font-size: 0.625rem;
  transition: all 0.2s;
}

.btn-cleanup:hover {
  background: #fef2f2;
  border-color: #fecaca;
  color: #dc2626;
}

.db-item.active .btn-cleanup {
  border-color: rgba(255, 255, 255, 0.3);
  color: rgba(255, 255, 255, 0.9);
}

.db-item.active .btn-cleanup:hover {
  background: rgba(239, 68, 68, 0.2);
  border-color: rgba(255, 255, 255, 0.5);
  color: white;
}

/* Archive Stats */
.archive-stats {
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #e5e7eb;
}

.archive-stats h4 {
  margin: 0 0 0.5rem 0;
  font-size: 0.75rem;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 600;
}

.archive-info {
  display: flex;
  justify-content: space-between;
  font-size: 0.6875rem;
  color: #9ca3af;
}

/* Main Content */
.database-content {
  flex: 1;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  overflow-y: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}

/* Query Mode */
.query-mode {
  padding: 1.5rem;
}

.query-editor h3 {
  margin: 0 0 1rem 0;
  color: #111827;
  font-size: 1.125rem;
  font-weight: 600;
}

.query-controls {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}

.db-selector {
  padding: 0.5rem;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  color: #374151;
  min-width: 200px;
}

.btn-execute {
  padding: 0.5rem 1rem;
  background: linear-gradient(135deg, #14b8a6 0%, #0d9488 100%);
  color: white;
  border: none;
  border-radius: 0.375rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  transition: all 0.2s;
}

.btn-execute:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 6px rgba(20, 184, 166, 0.3);
}

.btn-execute:disabled {
  background: #9ca3af;
  cursor: not-allowed;
  transform: none;
}

.query-input {
  width: 100%;
  min-height: 150px;
  padding: 0.75rem;
  background: #f9fafb;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  font-family: 'Monaco', 'Menlo', monospace;
  font-size: 0.875rem;
  color: #111827;
  resize: vertical;
}

.query-input:focus {
  outline: none;
  border-color: #14b8a6;
  background: white;
  box-shadow: 0 0 0 3px rgba(20, 184, 166, 0.1);
}

/* Table View */
.table-view {
  padding: 1.5rem;
}

.table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #e5e7eb;
}

.table-header h2 {
  margin: 0;
  color: #111827;
  font-size: 1.25rem;
  font-weight: 600;
}

.table-actions {
  display: flex;
  gap: 0.5rem;
}

.btn-export {
  padding: 0.375rem 0.75rem;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  color: #374151;
  cursor: pointer;
  font-size: 0.875rem;
  transition: all 0.2s;
}

.btn-export:hover {
  background: #f3f4f6;
  border-color: #14b8a6;
  color: #14b8a6;
}

/* Data Table */
.data-table-container {
  border: 1px solid #e5e7eb;
  border-radius: 0.375rem;
  overflow: hidden;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
}

.data-table th {
  background: #f9fafb;
  padding: 0.625rem;
  text-align: left;
  font-weight: 600;
  color: #374151;
  font-size: 0.8125rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #e5e7eb;
}

.data-table td {
  padding: 0.625rem;
  border-bottom: 1px solid #f3f4f6;
  color: #6b7280;
  font-size: 0.875rem;
}

.data-table tr:hover {
  background: #f9fafb;
}

/* Loading State */
.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem;
  color: #6b7280;
}

.loading-spinner {
  width: 2rem;
  height: 2rem;
  border: 3px solid #e5e7eb;
  border-top-color: #14b8a6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 1rem;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Empty State */
.empty-state {
  text-align: center;
  padding: 3rem;
  color: #9ca3af;
}

.empty-state i {
  font-size: 3rem;
  margin-bottom: 1rem;
  opacity: 0.5;
}

/* Table Buttons Container */
.table-buttons-container {
  padding: 1rem;
  border-bottom: 1px solid #e5e7eb;
}

.table-buttons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.table-button {
  padding: 0.375rem 0.75rem;
  background: #f0fdfb;
  color: #0d9488;
  border: 1px solid #ccfbf1;
  border-radius: 0.375rem;
  cursor: pointer;
  font-size: 0.8125rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.375rem;
  transition: all 0.2s;
}

.table-button:hover {
  background: #ccfbf1;
  border-color: #5eead4;
  transform: translateY(-1px);
}

.table-button.active {
  background: linear-gradient(135deg, #14b8a6 0%, #0d9488 100%);
  color: white;
  border-color: transparent;
  box-shadow: 0 2px 4px rgba(20, 184, 166, 0.2);
}

.table-button i {
  font-size: 0.75rem;
  opacity: 0.8;
}

.table-name {
  font-weight: 500;
}

.row-badge {
  padding: 0.125rem 0.375rem;
  background: rgba(20, 184, 166, 0.1);
  border-radius: 0.25rem;
  font-size: 0.6875rem;
  font-weight: 600;
  color: #0d9488;
}

.table-button.active .row-badge {
  background: rgba(255, 255, 255, 0.2);
  color: white;
}`, "",{"version":3,"sources":["webpack://./src/components/Database.css"],"names":[],"mappings":"AAAA;;;EAGE;;AAEF;EACE,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,gBAAgB;EAChB,iBAAiB;EACjB,gCAAgC;EAChC,0CAA0C;AAC5C;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,YAAY;EACZ,iBAAiB;EACjB,SAAS;EACT,cAAc;EACd,gBAAgB;AAClB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,YAAY;AACd;;AAEA;;EAEE,oBAAoB;EACpB,iBAAiB;EACjB,yBAAyB;EACzB,uBAAuB;EACvB,cAAc;EACd,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,WAAW;EACX,oBAAoB;EACpB,mBAAmB;EACnB,gBAAgB;AAClB;;AAEA;;EAEE,mBAAmB;EACnB,qBAAqB;EACrB,cAAc;EACd,2BAA2B;AAC7B;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,cAAc;EACd,aAAa;EACb,mBAAmB;EACnB,YAAY;EACZ,YAAY;EACZ,uBAAuB;AACzB;;AAEA;EACE,OAAO;EACP,aAAa;EACb,gBAAgB;EAChB,aAAa;EACb,SAAS;AACX;;AAEA,qBAAqB;AACrB;EACE,YAAY;EACZ,iBAAiB;EACjB,yBAAyB;EACzB,qBAAqB;EACrB,gBAAgB;EAChB,aAAa;EACb,0CAA0C;AAC5C;;AAEA;EACE,qBAAqB;EACrB,mBAAmB;EACnB,cAAc;EACd,yBAAyB;EACzB,sBAAsB;EACtB,gBAAgB;AAClB;;AAEA;EACE,sBAAsB;EACtB,mBAAmB;EACnB,yBAAyB;EACzB,uBAAuB;EACvB,wBAAwB;EACxB,eAAe;EACf,oBAAoB;AACtB;;AAEA;EACE,mBAAmB;EACnB,qBAAqB;EACrB,0BAA0B;AAC5B;;AAEA;EACE,6DAA6D;EAC7D,YAAY;EACZ,yBAAyB;EACzB,6CAA6C;AAC/C;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,aAAa;EACb,oBAAoB;EACpB,gBAAgB;AAClB;;AAEA;EACE,oBAAoB;EACpB,YAAY;AACd;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,oBAAoB;EACpB,cAAc;EACd,eAAe;EACf,iBAAiB;AACnB;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,0BAA0B;EAC1B,uBAAuB;EACvB,yBAAyB;EACzB,sBAAsB;EACtB,cAAc;EACd,eAAe;EACf,mBAAmB;EACnB,oBAAoB;AACtB;;AAEA;EACE,mBAAmB;EACnB,qBAAqB;EACrB,cAAc;AAChB;;AAEA;EACE,sCAAsC;EACtC,+BAA+B;AACjC;;AAEA;EACE,kCAAkC;EAClC,sCAAsC;EACtC,YAAY;AACd;;AAEA,kBAAkB;AAClB;EACE,gBAAgB;EAChB,iBAAiB;EACjB,6BAA6B;AAC/B;;AAEA;EACE,oBAAoB;EACpB,kBAAkB;EAClB,cAAc;EACd,yBAAyB;EACzB,sBAAsB;EACtB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,oBAAoB;EACpB,cAAc;AAChB;;AAEA,iBAAiB;AACjB;EACE,OAAO;EACP,iBAAiB;EACjB,yBAAyB;EACzB