ont-size: 0.875rem;\n  color: #6b7280;\n  text-transform: uppercase;\n  letter-spacing: 0.05em;\n  font-weight: 600;\n}\n\n.db-item {\n  margin-bottom: 0.25rem;\n  background: #f9fafb;\n  border: 1px solid #e5e7eb;\n  border-radius: 0.375rem;\n  padding: 0.375rem 0.5rem;\n  cursor: pointer;\n  transition: all 0.2s;\n}\n\n.db-item:hover {\n  background: #f3f4f6;\n  border-color: #14b8a6;\n  transform: translateX(2px);\n}\n\n.db-item.active {\n  background: linear-gradient(135deg, #14b8a6 0%, #0d9488 100%);\n  color: white;\n  border-color: transparent;\n  box-shadow: 0 2px 4px rgba(20, 184, 166, 0.2);\n}\n\n.db-item-content {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.db-name {\n  display: flex;\n  align-items: center;\n  gap: 0.375rem;\n  font-size: 0.8125rem;\n  font-weight: 500;\n}\n\n.db-name i {\n  font-size: 0.6875rem;\n  opacity: 0.8;\n}\n\n.db-info-right {\n  display: flex;\n  align-items: center;\n  gap: 0.5rem;\n}\n\n.db-size {\n  font-size: 0.6875rem;\n  color: #9ca3af;\n  min-width: 50px;\n  text-align: right;\n}\n\n.db-item.active .db-size {\n  color: rgba(255, 255, 255, 0.85);\n}\n\n.btn-cleanup {\n  padding: 0.125rem 0.375rem;\n  background: transparent;\n  border: 1px solid #d1d5db;\n  border-radius: 0.25rem;\n  color: #6b7280;\n  cursor: pointer;\n  font-size: 0.625rem;\n  transition: all 0.2s;\n}\n\n.btn-cleanup:hover {\n  background: #fef2f2;\n  border-color: #fecaca;\n  color: #dc2626;\n}\n\n.db-item.active .btn-cleanup {\n  border-color: rgba(255, 255, 255, 0.3);\n  color: rgba(255, 255, 255, 0.9);\n}\n\n.db-item.active .btn-cleanup:hover {\n  background: rgba(239, 68, 68, 0.2);\n  border-color: rgba(255, 255, 255, 0.5);\n  color: white;\n}\n\n/* Archive Stats */\n.archive-stats {\n  margin-top: 1rem;\n  padding-top: 1rem;\n  border-top: 1px solid #e5e7eb;\n}\n\n.archive-stats h4 {\n  margin: 0 0 0.5rem 0;\n  font-size: 0.75rem;\n  color: #6b7280;\n  text-transform: uppercase;\n  letter-spacing: 0.05em;\n  font-weight: 600;\n}\n\n.archive-info {\n  display: flex;\n  justify-content: space-between;\n  font-size: 0.6875rem;\n  color: #9ca3af;\n}\n\n/* Main Content */\n.database-content {\n  flex: 1;\n  background: white;\n  border: 1px solid #e5e7eb;\n  border-radius: 0.5rem;\n  overflow-y: auto;\n  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);\n}\n\n/* Query Mode */\n.query-mode {\n  padding: 1.5rem;\n}\n\n.query-editor h3 {\n  margin: 0 0 1rem 0;\n  color: #111827;\n  font-size: 1.125rem;\n  font-weight: 600;\n}\n\n.query-controls {\n  display: flex;\n  gap: 1rem;\n  margin-bottom: 1rem;\n}\n\n.db-selector {\n  padding: 0.5rem;\n  background: white;\n  border: 1px solid #d1d5db;\n  border-radius: 0.375rem;\n  color: #374151;\n  min-width: 200px;\n}\n\n.btn-execute {\n  padding: 0.5rem 1rem;\n  background: linear-gradient(135deg, #14b8a6 0%, #0d9488 100%);\n  color: white;\n  border: none;\n  border-radius: 0.375rem;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  gap: 0.5rem;\n  font-weight: 500;\n  transition: all 0.2s;\n}\n\n.btn-execute:hover {\n  transform: translateY(-1px);\n  box-shadow: 0 4px 6px rgba(20, 184, 166, 0.3);\n}\n\n.btn-execute:disabled {\n  background: #9ca3af;\n  cursor: not-allowed;\n  transform: none;\n}\n\n.query-input {\n  width: 100%;\n  min-height: 150px;\n  padding: 0.75rem;\n  background: #f9fafb;\n  border: 1px solid #d1d5db;\n  border-radius: 0.375rem;\n  font-family: 'Monaco', 'Menlo', monospace;\n  font-size: 0.875rem;\n  color: #111827;\n  resize: vertical;\n}\n\n.query-input:focus {\n  outline: none;\n  border-color: #14b8a6;\n  background: white;\n  box-shadow: 0 0 0 3px rgba(20, 184, 166, 0.1);\n}\n\n/* Table View */\n.table-view {\n  padding: 1.5rem;\n}\n\n.table-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 1rem;\n  padding-bottom: 1rem;\n  border-bottom: 1px solid #e5e7eb;\n}\n\n.table-header h2 {\n  margin: 0;\n  color: #111827;\n  font-size: 1.25rem;\n  font-weight: 600;\n}\n\n.table-actions {\n  display: flex;\n  gap: 0.5rem;\n}\n\n.btn-export {\n  padding: 0.375rem 0.75rem;\n  background: white;\n  border: 1px solid #d1d5db;\n  border-radius: 0.375rem;\n  color: #374151;\n  cursor: pointer;\n  font-size: 0.875rem;\n  transition: all 0.2s;\n}\n\n.btn-export:hover {\n  background: #f3f4f6;\n  border-color: #14b8a6;\n  color: #14b8a6;\n}\n\n/* Data Table */\n.data-table-container {\n  border: 1px solid #e5e7eb;\n  border-radius: 0.375rem;\n  overflow: hidden;\n}\n\n.data-table {\n  width: 100%;\n  border-collapse: collapse;\n}\n\n.data-table th {\n  background: #f9fafb;\n  padding: 0.625rem;\n  text-align: left;\n  font-weight: 600;\n  color: #374151;\n  font-size: 0.8125rem;\n  text-transform: uppercase;\n  letter-spacing: 0.05em;\n  border-bottom: 1px solid #e5e7eb;\n}\n\n.data-table td {\n  padding: 0.625rem;\n  border-bottom: 1px solid #f3f4f6;\n  color: #6b7280;\n  font-size: 0.875rem;\n}\n\n.data-table tr:hover {\n  background: #f9fafb;\n}\n\n/* Loading State */\n.loading-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 3rem;\n  color: #6b7280;\n}\n\n.loading-spinner {\n  width: 2rem;\n  height: 2rem;\n  border: 3px solid #e5e7eb;\n  border-top-color: #14b8a6;\n  border-radius: 50%;\n  animation: spin 1s linear infinite;\n  margin-right: 1rem;\n}\n\n@keyframes spin {\n  to { transform: rotate(360deg); }\n}\n\n/* Empty State */\n.empty-state {\n  text-align: center;\n  padding: 3rem;\n  color: #9ca3af;\n}\n\n.empty-state i {\n  font-size: 3rem;\n  margin-bottom: 1rem;\n  opacity: 0.5;\n}\n\n/* Table Buttons Container */\n.table-buttons-container {\n  padding: 1rem;\n  border-bottom: 1px solid #e5e7eb;\n}\n\n.table-buttons {\n  display: flex;\n  gap: 0.5rem;\n  flex-wrap: wrap;\n}\n\n.table-button {\n  padding: 0.375rem 0.75rem;\n  background: #f0fdfb;\n  color: #0d9488;\n  border: 1px solid #ccfbf1;\n  border-radius: 0.375rem;\n  cursor: pointer;\n  font-size: 0.8125rem;\n  font-weight: 500;\n  display: flex;\n  align-items: center;\n  gap: 0.375rem;\n  transition: all 0.2s;\n}\n\n.table-button:hover {\n  background: #ccfbf1;\n  border-color: #5eead4;\n  transform: translateY(-1px);\n}\n\n.table-button.active {\n  background: linear-gradient(135deg, #14b8a6 0%, #0d9488 100%);\n  color: white;\n  border-color: transparent;\n  box-shadow: 0 2px 4px rgba(20, 184, 166, 0.2);\n}\n\n.table-button i {\n  font-size: 0.75rem;\n  opacity: 0.8;\n}\n\n.table-name {\n  font-weight: 500;\n}\n\n.row-badge {\n  padding: 0.125rem 0.375rem;\n  background: rgba(20, 184, 166, 0.1);\n  border-radius: 0.25rem;\n  font-size: 0.6875rem;\n  font-weight: 600;\n  color: #0d9488;\n}\n\n.table-button.active .row-badge {\n  background: rgba(255, 255, 255, 0.2);\n  color: white;\n}"],"sourceRoot":""}]);
