r;\n  gap: 1.5rem;\n}\n\n/* Database Sidebar */\n.database-sidebar {\n  background: white;\n  border-radius: 0.75rem;\n  padding: 1.25rem;\n  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);\n  max-height: 600px;\n  overflow-y: auto;\n}\n\n.database-sidebar h3 {\n  font-size: 0.875rem;\n  font-weight: 600;\n  color: #6b7280;\n  margin: 0 0 1rem 0;\n  text-transform: uppercase;\n  letter-spacing: 0.05em;\n}\n\n.db-item {\n  padding: 0.5rem 0.75rem;\n  margin-bottom: 0.375rem;\n  border-radius: 0.5rem;\n  cursor: pointer;\n  transition: all 0.2s;\n  background: #f9fafb;\n  border: 1px solid #e5e7eb;\n}\n\n.db-item:hover {\n  background: #f3f4f6;\n  border-color: #14b8a6;\n  transform: translateX(2px);\n}\n\n.db-item.active {\n  background: linear-gradient(135deg, #14b8a6 0%, #0d9488 100%);\n  color: white;\n  border-color: transparent;\n  box-shadow: 0 2px 4px rgba(20, 184, 166, 0.2);\n}\n\n.db-name {\n  font-weight: 500;\n  font-size: 0.875rem;\n  margin-bottom: 0.125rem;\n  display: flex;\n  align-items: center;\n  gap: 0.5rem;\n}\n\n.db-name i {\n  font-size: 0.75rem;\n  opacity: 0.8;\n}\n\n.db-size {\n  font-size: 0.75rem;\n  opacity: 0.7;\n  margin-left: 1.25rem;\n}\n\n.db-actions {\n  margin-top: 0.5rem;\n  padding-top: 0.5rem;\n  border-top: 1px solid rgba(229, 231, 235, 0.5);\n}\n\n.btn-cleanup {\n  background: transparent;\n  border: 1px solid #e5e7eb;\n  color: #6b7280;\n  padding: 0.25rem 0.5rem;\n  border-radius: 0.25rem;\n  cursor: pointer;\n  font-size: 0.75rem;\n  transition: all 0.2s;\n}\n\n.btn-cleanup:hover {\n  background: #fef2f2;\n  border-color: #fecaca;\n  color: #dc2626;\n}\n\n/* Archive Stats */\n.archive-stats {\n  margin-top: 1.5rem;\n  padding-top: 1rem;\n  border-top: 1px solid #e5e7eb;\n}\n\n.archive-stats h4 {\n  font-size: 0.75rem;\n  font-weight: 600;\n  color: #6b7280;\n  margin: 0 0 0.5rem 0;\n  text-transform: uppercase;\n  letter-spacing: 0.05em;\n}\n\n.archive-info {\n  display: flex;\n  justify-content: space-between;\n  font-size: 0.75rem;\n  color: #9ca3af;\n}\n\n.database-content {\n  display: flex;\n  flex-direction: column;\n  gap: 1rem;\n}\n\n.database-list {\n  background: white;\n  border-radius: 0.75rem;\n  padding: 1rem;\n  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);\n  max-height: 600px;\n  overflow-y: auto;\n}\n\n.database-list h2 {\n  font-size: 1.125rem;\n  font-weight: 600;\n  color: #374151;\n  margin: 0 0 1rem 0;\n  padding-bottom: 0.5rem;\n  border-bottom: 1px solid #e5e7eb;\n}\n\n.db-item {\n  padding: 0.75rem;\n  margin-bottom: 0.5rem;\n  border-radius: 0.5rem;\n  cursor: pointer;\n  transition: all 0.2s;\n  background: #f9fafb;\n  border: 1px solid transparent;\n}\n\n.db-item:hover {\n  background: #f3f4f6;\n  border-color: #d1d5db;\n}\n\n.db-item.selected {\n  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\n  color: white;\n  border-color: transparent;\n}\n\n.db-name {\n  font-weight: 600;\n  margin-bottom: 0.25rem;\n}\n\n.db-info {\n  font-size: 0.75rem;\n  opacity: 0.8;\n}\n\n.table-view {\n  background: white;\n  border-radius: 0.75rem;\n  padding: 1.5rem;\n  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);\n}\n\n.table-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 1.5rem;\n  padding-bottom: 1rem;\n  border-bottom: 2px solid #e5e7eb;\n}\n\n.table-header h2 {\n  font-size: 1.5rem;\n  font-weight: 600;\n  color: #111827;\n  margin: 0;\n}\n\n.table-tabs {\n  display: flex;\n  gap: 0.5rem;\n  margin-bottom: 1.5rem;\n  border-bottom: 1px solid #e5e7eb;\n}\n\n.tab {\n  padding: 0.5rem 1rem;\n  background: none;\n  border: none;\n  border-bottom: 2px solid transparent;\n  color: #6b7280;\n  cursor: pointer;\n  transition: all 0.2s;\n  font-size: 0.875rem;\n  font-weight: 500;\n}\n\n.tab:hover {\n  color: #374151;\n}\n\n.tab.active {\n  color: #7c3aed;\n  border-bottom-color: #7c3aed;\n}\n\n.query-section {\n  margin-bottom: 1.5rem;\n}\n\n.query-input {\n  width: 100%;\n  min-height: 100px;\n  padding: 0.75rem;\n  border: 1px solid #d1d5db;\n  border-radius: 0.5rem;\n  font-family: 'Monaco', 'Menlo', monospace;\n  font-size: 0.875rem;\n  resize: vertical;\n  background: #f9fafb;\n}\n\n.query-input:focus {\n  outline: none;\n  border-color: #7c3aed;\n  box-shadow: 0 0 0 3px rgba(124, 58, 237, 0.1);\n}\n\n.query-controls {\n  display: flex;\n  gap: 1rem;\n  margin-top: 1rem;\n}\n\n.btn {\n  padding: 0.5rem 1rem;\n  border-radius: 0.5rem;\n  font-weight: 500;\n  cursor: pointer;\n  transition: all 0.2s;\n  border: none;\n  font-size: 0.875rem;\n}\n\n.btn-primary {\n  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\n  color: white;\n}\n\n.btn-primary:hover {\n  transform: translateY(-1px);\n  box-shadow: 0 4px 12px rgba(124, 58, 237, 0.3);\n}\n\n.btn-primary:disabled {\n  opacity: 0.5;\n  cursor: not-allowed;\n  transform: none;\n}\n\n.btn-secondary {\n  background: #f3f4f6;\n  color: #374151;\n  border: 1px solid #d1d5db;\n}\n\n.btn-secondary:hover {\n  background: #e5e7eb;\n}\n\n.data-table {\n  width: 100%;\n  border-collapse: collapse;\n  font-size: 0.875rem;\n}\n\n.data-table th {\n  background: #f9fafb;\n  padding: 0.75rem;\n  text-align: left;\n  font-weight: 600;\n  color: #374151;\n  border-bottom: 2px solid #e5e7eb;\n  position: sticky;\n  top: 0;\n  z-index: 10;\n}\n\n.data-table td {\n  padding: 0.75rem;\n  border-bottom: 1px solid #f3f4f6;\n  color: #6b7280;\n}\n\n.data-table tr:hover {\n  background: #f9fafb;\n}\n\n.data-table tr:nth-child(even) {\n  background: #fafbfc;\n}\n\n.table-scroll {\n  max-height: 400px;\n  overflow-y: auto;\n  border: 1px solid #e5e7eb;\n  border-radius: 0.5rem;\n}\n\n.loading {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 3rem;\n  color: #6b7280;\n}\n\n.loading-spinner {\n  width: 2rem;\n  height: 2rem;\n  border: 3px solid #e5e7eb;\n  border-top-color: #7c3aed;\n  border-radius: 50%;\n  animation: spin 1s linear infinite;\n  margin-right: 1rem;\n}\n\n@keyframes spin {\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n.error {\n  background: #fef2f2;\n  color: #dc2626;\n  padding: 1rem;\n  border-radius: 0.5rem;\n  border: 1px solid #fecaca;\n  margin-bottom: 1rem;\n}\n\n.empty-state {\n  text-align: center;\n  padding: 3rem;\n  color: #9ca3af;\n}\n\n.empty-state svg {\n  width: 4rem;\n  height: 4rem;\n  margin: 0 auto 1rem;\n  opacity: 0.5;\n}\n\n.query-result {\n  margin-top: 1.5rem;\n  padding: 1rem;\n  background: #f9fafb;\n  border-radius: 0.5rem;\n  border: 1px solid #e5e7eb;\n}\n\n.result-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 1rem;\n  padding-bottom: 0.5rem;\n  border-bottom: 1px solid #e5e7eb;\n}\n\n.result-info {\n  font-size: 0.875rem;\n  color: #6b7280;\n}\n\n.mode-toggle {\n  display: flex;\n  align-items: center;\n  gap: 0.5rem;\n  padding: 0.5rem 1rem;\n  background: #f3f4f6;\n  border-radius: 0.5rem;\n  cursor: pointer;\n  transition: all 0.2s;\n}\n\n.mode-toggle:hover {\n  background: #e5e7eb;\n}\n\n.mode-toggle.active {\n  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\n  color: white;\n}\n\n/* Table Buttons - Styled like sidebar buttons */\n.table-buttons-container {\n  background: white;\n  border-radius: 0.75rem;\n  padding: 1rem;\n  margin-bottom: 1.5rem;\n  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);\n}\n\n.table-buttons {\n  display: flex;\n  gap: 0.5rem;\n  flex-wrap: wrap;\n}\n\n.table-button {\n  display: flex;\n  align-items: center;\n  gap: 0.5rem;\n  padding: 0.625rem 1rem;\n  background: linear-gradient(135deg, #1e293b 0%, #334155 100%);\n  color: white;\n  border: none;\n  border-radius: 0.5rem;\n  cursor: pointer;\n  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n  font-size: 0.875rem;\n  font-weight: 500;\n  position: relative;\n  overflow: hidden;\n}\n\n.table-button::before {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: linear-gradient(135deg, rgba(139, 92, 246, 0.2) 0%, rgba(59, 130, 246, 0.2) 100%);\n  opacity: 0;\n  transition: opacity 0.3s;\n}\n\n.table-button:hover {\n  transform: translateY(-2px);\n  box-shadow: 0 10px 20px -5px rgba(59, 130, 246, 0.4);\n}\n\n.table-button:hover::before {\n  opacity: 1;\n}\n\n.table-button.active {\n  background: linear-gradient(135deg, #8b5cf6 0%, #3b82f6 100%);\n  box-shadow: 0 8px 16px -4px rgba(139, 92, 246, 0.5);\n}\n\n.table-button.active::after {\n  content: '';\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n  height: 3px;\n  background: linear-gradient(90deg, #fbbf24, #f59e0b);\n  animation: shimmer 2s infinite;\n}\n\n@keyframes shimmer {\n  0% { transform: translateX(-100%); }\n  100% { transform: translateX(100%); }\n}\n\n.table-button i {\n  font-size: 0.875rem;\n  opacity: 0.9;\n}\n\n.table-name {\n  font-weight: 500;\n  text-transform: capitalize;\n}\n\n.row-badge {\n  background: rgba(255, 255, 255, 0.2);\n  padding: 0.125rem 0.5rem;\n  border-radius: 9999px;\n  font-size: 0.75rem;\n  font-weight: 600;\n  backdrop-filter: blur(4px);\n}\n\n.table-button:active {\n  transform: scale(0.98);\n}\n\n/* Responsive adjustments */\n@media (max-width: 768px) {\n  .database-content {\n    grid-template-columns: 1fr;\n  }\n  \n  .database-list {\n    max-height: 200px;\n  }\n  \n  .stats-grid {\n    grid-template-columns: 1fr;\n  }\n  \n  .table-buttons {\n    flex-direction: column;\n  }\n  \n  .table-button {\n    width: 100%;\n    justify-content: space-between;\n  }\n}"],"sourceRoot":""}]);
